import Vue from 'vue'
import VueRouter from 'vue-router'
import page404 from 'temp/404'

Vue.use(VueRouter)

const router = new VueRouter({
	// mode: 'history',
	routes: [
		{
			path: '*',
			name: '404',
			component: page404
		},
		{
			path: '/',
			name: 'index'
		},
		{
			path: '/about',
			name: 'about',
			component: () => import('temp/about')
		},
		{
			path: '/works',
			name: 'works',
			component: () => import('temp/works/index')
		},
		{
			path: '/contacts',
			name: 'contacts',
			component: () => import('temp/contacts')
		},
	],
	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition
		} else {
			return { x: 0, y: 0 }
		}
	}

})

export default router
