<template lang="pug">

.nav(:class="{ hide }")
	router-link.item.logo(to="/" :class="{ hide, active: page == '404' }" :style="{left: active != -1 ? active * 33.3 + 'vw' : '33.3vw'}")
		span bunnyonthemoon

	router-link.item(v-for="(item, index) in nav" :key="item.link" :style="{left: index * 33.3 + 'vw'}" :to="item.link" :class="{ active: item.link == page }")
		span {{item.title}}

</template>

<script>
export default {
	data(){
		return {
			nav: [
				{
					title: 'Обо мне',
					link: 'about'
				},
				{
					title: 'Работы',
					link: 'works'
				},
				{
					title: 'Контакты',
					link: 'contacts'
				}
			]
		}
	},
	computed:{
		hide(){
			return this.$route.name != 'index'
		},
		page(){
			return this.$route.name
		},
		active(){
			return this.nav.findIndex(item => item.link == this.page) || null
		}
	}
}
</script>

<style lang="stylus" scoped>

.nav
	.item
		position fixed
		z-index 10
		top 0
		height 100%
		height 100vh
		width 33.3%
		width 33.3vw
		cursor pointer
		display flex
		align-items center
		justify-content center
		transition height .4s, top .4s
		font-size 30px
		span
			transition transform .4s, font-size .4s
			transform scale(1)
		&:hover
			span
				transform scale(1.1)
	.logo
		z-index 11
		height 40px
		top 20px
		transition height .4s, top .4s, left .4s
	&.hide
		.item
			height: 45px
			font-size: 20px
			&.active
				top calc(100% - 45px)
		.logo
			top 0

@media screen and (max-width: 600px)
	.nav
		.item
			font-size 20px
		&.hide
			.item
				font-size 16px
				height 35px
				&.active
					top calc(100% - 35px)
			.logo
				font-size 12px


</style>