import Vue from 'vue'
import Vuex from 'vuex'
// import Axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		worksPage: 0,
		worksFixed: false,
		worksResize: false,
		worksResizeCount: 0
	},
	mutations: {
		worksPage: (store, index) => {
			store.worksPage = index
		},
		worksFixed: (store, val) => {
			store.worksFixed = val
		},
		worksResize: (store, val) => {
			if (val == -1) val = store.worksResizeCount - 1
			if (val > 0 && store.worksResizeCount == 0) store.worksResize = true
			else if (val == 0 && store.worksResizeCount > 0) store.worksResize = false
			store.worksResizeCount = val
		}
	},
	actions: {
	}
})